
.flex>.card{
    margin: 20px;
}
.card{
    width: 250px;
    height: 400px;
    border-radius: 20px;
    position: relative;
    background-color: #232323;
    
}
.circle{
    z-index: 1;
    position: absolute;
    top:0;
    left: 0;
    width: 100%;
    height: 100%;
    clip-path:circle(100px at 80% 20%);
    border-radius: 20px;
    transition: 0.5s ease-in-out;
}

#api img{
    z-index: 2;
    position: absolute;
    height: 80px;
    top: 80px;
    left: 12%;
    width: auto;
    transition:  0.5s ease-in-out;
}


#api .card:hover img{
    top: -20px;
    left: -30px;
}
#api .card:hover .circle{
    clip-path:circle(200px at 80% -20%);
}
#api .card:hover .card-container{
    bottom: 10px;
}
#api .card:hover .color-flex{
    opacity: 1;
}
.card-overlay{
    position: absolute;
    height: 100%;
    width: 100%;
    overflow: hidden;
}
.card-overlay:after{
    content: 'Country';
    position: absolute;
    left: -10%;
    top: 40%;
    font-size: 5em;
    font-weight: 800;
    font-style: italic;
    color:rgba(255,255,255,0.04);
}
.card-container{
    display: flex;
    flex-direction: column;
    position: absolute;
    bottom: -20px;
    align-items: center;
    justify-content: center;
    width: 100%;
    transition: 0.5s ease-in-out;
}
.card-container>h2{
    margin:5px;
    color: whitesmoke;
}
h2, button{
    color: whitesmoke;
}
.color-flex{
    z-index: 1;
    display: flex;
    flex-direction: row;
    align-items: center;
    transition-delay: 1s;
    transition: 0.5s ease-in-out;
    opacity: 0;
}
.color-flex > span{
    border-radius: 50%;
    margin: 5px;
    width: 20px;
    height: 20px;
    cursor: pointer;
   
}
.name-style{
   margin: 5px;
}


